
let data={
    datalist:[],
    shoucang:[],
    userinfo:''
}

const homereducer=(state=data,action)=>{
    if(action.type=='LOGIN'){        
        console.log(action.username);
       return Object.assign({},state,{userinfo:action.username})
    }
    else if(action.type=='GETLIST'){
        
            return Object.assign({},state,{datalist:action.list})
        
    }else if(action.type=='SHOUCANG'){
        if(state.shoucang.every(item=>{
            return item.id!==action.data.id;
        })){     
            return Object.assign({},state,{shoucang:[...state.shoucang,action.data]});
        }else{
            return state;
        }       
    }else if(action.type=='DELETE'){
        let sc=state.shoucang.filter(item=>{
            if(item.id==action.data){
                return false;
            }else{
                return true;
            }
        })
        return Object.assign({},state,{shoucang:sc});
    }else{
        return state;
    }
}
export default homereducer;