import React from 'react'
import './home.css'
import { NavLink } from 'react-router-dom'

const Goods = (props) => {
    return (
        <div>
            <NavLink to={{pathname:'/home/detail',state:{data:props.data}}}>
                <div className='pictureList'>
                    <img style={{ width: '100%', height: '80%' }}
                        src={props.data.img} />
                    <div style={{ color: 'black', margin: '0 auto', fontFamily: 'Microsoft soft', width: '80%', fontSize: '100%', lineHeight: '20px', height: '20px', overflow: 'hidden' }}>
                        {props.data.title}
                    </div>
                    <div style={{ width: '80%', margin: '0 auto', height: '20px' }}>
                        <span>￥</span>
                        <span className='money'>{props.data.price}</span>
                    </div>
                </div>
            </NavLink>
        </div>
    )
}

export default Goods;
