import React from 'react'
import {del} from './actionCreator'
import {connect} from 'react-redux'
const Shoucang = (props) => {
    return (
        <div style={{width:'100%',height:'150px',backgroundColor:'white',marginTop:'10px'}}>
            <img style={{ width: '30%',margin:'20px'}}
                        src={props.data.img} />
            <div style={{float:'right',marginRight:'10%',marginTop:'20px'}}>
                <div style={{fontFamily:'cursive',fontSize:'15px',width:'150px',height:'30px'}}>{props.data.title}</div>
                <div style={{fontFamily:'cursive',fontSize:'15px',marginTop:'5px',width:'150px',height:'30px'}}>价格：{props.data.price}</div>
                <button onClick={()=>props.dispatch(del(props.data.id))}>取消收藏</button>
            </div>
        </div>
    )
}

export default connect((store)=>{
    return store;
})(Shoucang);
