import React from 'react'
import { NavBar, Icon } from 'antd-mobile';
import './home.css'
import DetailTab from './DetailTab'
const Detail = (props) => {
    console.log(props);
    return (
        <div>
            <div>
                <NavBar style={{width:'30px',height:'30px',borderRadius:'50%',display:'flex',textAlign: 'center'
                        ,background: 'rgba(0, 0, 0, 0.4)',color: '#fff',border:'0',position: 'absolute',
                        top:'0.2rem',left: '0px'}}
                mode="light"
                icon={ <Icon type="left" style={{position:'absolute',left:'3px',top:'5px'}}/> }
                onLeftClick={ () => props.history.goBack() }
            ></NavBar>
            </div>
            <img style={{width:'100%'}}
            src={props.location.state.data.img}/>
            <DetailTab data={props}/>
        </div>
    )
}

export default Detail
