import React from 'react'
import './home.css'
import {NavLink} from 'react-router-dom'
import {connect} from 'react-redux'
import Slideshow from './Slideshow'
import Goods from './Goods'
import {getlist} from './actionCreator'
// iconfont.cn官网搜选图片，加入购物车
// 点击页面右上角购物车，将图标添加进项目
// 点击‘下载到本地 ’
//解压压缩包，全部复制到项目public文件夹下，
//在index.html中通过link引入到iconfont.css
//使用时，<i className='iconfont icon-具体名字></>

const Home = (props) => {
    props.dispatch(getlist());
    return (
        <div>
            {/* <img style={{width:'100%'}} src={require()} alt=''/>
            <i style={{fontSize:'100',color:'red'}}/> */}
            <div style={{height: '25px', background: 'rgb(255, 133, 42)',paddingTop:'8px',paddingBottom:'8px'}}>
                <i style={{fontSize:'20px',color:'white',marginLeft:'2%'}} className='iconfont icon-taobao'/>
                <NavLink to='/home/search' className='nav' style={{float:'right',textAlign:'center',width:'90%', height: '25px'
                ,display:'block',background:'rgb(255, 78, 34)',marginRight:'0.5%'
                ,borderRadius: '4px'}}>
                    <span style={{color:'white',lineHeight:'25px'}}><i style={{color:'white'}} className='iconfont icon-icon-search' />
                    寻找宝贝店铺</span>             
                </NavLink>
            </div>
            <div>
                {/* 走马灯*/}
                <div style={{height:'160px'}}>
                    <Slideshow />
                </div>
                {/* 你可能还喜欢 */}
                <div style={{height:'25px',padding:'7px 0',marginTop:'15px'}}>
                    <div style={{margin:'0px auto',width:'170px',height:'20px',color:'rgb(255, 78, 34)'}}>
                        ——
                        <i style={{color:'rgb(255, 78, 34)'}} className='iconfont icon-xihuan1'></i>
                        <div style={{width:'120px',height:'20px',margin:'0',float:'right'}}>
                            你可能还喜欢——</div>
                    </div>
                </div>
            </div>
            <div style={{minHeight:'1700px'}}>
                {/* 商品列表 */}
                {
                    props.datalist.map((items,idx)=>{
                        return <Goods data={items} history={props.history} key={idx}/>
                    })
                }
            </div>
        </div>   
    )
}

export default connect((store)=>{
    return store;
})(Home);