
const login = () => {
    return(dispatch)=>{
    let url = 'https://www.fastmock.site/mock/9e4bbf9e35ad15942010865690c87ac6/api/login'
    fetch(url, {
        method: 'GET',
        headers: {
            'Content-type': 'application/json'
        },
    }).then(res => res.json())
        .then(res => {
            dispatch({
                type:'LOGIN',
                username:res.data.name
            })
        })
    }
}

const getlist = () => {
    return(dispatch)=>{
    let url = 'https://www.fastmock.site/mock/9e4bbf9e35ad15942010865690c87ac6/api/getgoodslist'
    fetch(url, {
        method: 'get',
        headers: {
            'Content-type': 'application/json'
        },
    }).then(res => res.json())
        .then(res => {
            dispatch({
                type:'GETLIST',
                list:res.data
            })
        })
    }
}
function shoucang(dat){
    // console.log(dat);
    return{
        type:'SHOUCANG',
        data:dat
    }
}

function del(id){
    return{
        type:'DELETE',
        data:id
    }
    
}

export { login,getlist,shoucang,del }