import React from 'react'
import './home.css'
import {login} from './actionCreator'
import {connect} from 'react-redux'
const Login = (props) => {
    return (
            
        <div>
            {props.userinfo===''
            ?<div>

                <i style={{fontSize:'50px',color: 'rgb(255, 133, 42)',marginLeft:'45%'}} className='iconfont icon-taobao'/>
                <input type='text' style={{width:'85%'}} className='text' placeholder='手机号/邮箱。会员名'/>
    
                <input type='password' style={{width:'85%'}} className='text' placeholder='请输入登录密码'/>
    
                <button onClick={()=>props.dispatch(login())}
                style={{backgroundColor:'rgb(255, 133, 42)',color:'white' ,marginTop:'30px',marginLeft:'10%',width:'75%',height:'30px',border:'0',borderRadius:'35px'}}>登录</button>
            </div>
            :props.history.goBack()}
        </div>
    )
}

export default connect((store)=>{
    return store;
})(Login);
